* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: linear-gradient(#ddd, #2c2c2c) no-repeat;

}

.App {
  text-align: center;
}

h1,
h3 {
  color: #000;
  /* text-shadow: 2px 2px 2px rgb(255, 255, 255); */
  margin-top: 20px;
}

.intro {
  height: 100vh;
  background-image: url('./imgs/background.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  flex-direction: column;
  box-shadow: 0px 10px 10px #000;
  align-items: center;
    justify-content: center;
}

.intro > h1 {
  color: #ddd;
  text-shadow: 0 1px 2px #000;
  position: absolute;
  top: 10%;
}

.form-input {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  padding: 2em;
}

.form-input input {
  height: 2em;
  margin-top: 55px;
  outline: none;
  transition: .5s all;
  align-self: center;
  border: none;
  border: 1px solid #ddd;
  border-radius: 10px
}

.form-input input:focus {
  border-radius: 20px;
}

.form-input input:focus~input[type="submit"] {
  visibility: visible;
}




input[type="submit"] {
  background: #2c2c2c;
  
  -webkit-transition: all .4s;
  transition: all .4s;
  color: #fff;
  cursor: pointer;
  height: 42px;
  font-size: 1.1em;
  border: #ddd;
  border-radius: 5px;
  width: 100px;
  margin-left: 15px;
}

input[type="submit"]:hover {
  background: #ddd;
  color: #2c2c2c;
}
.form-input input[type="text"] {
  font-size: 1.2em;
  text-align: center;
}

ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5em;
}

ul li img {
  width: 40%;
}

ul li a {
  color: #fff;
  text-decoration: none;
  font-size: 1.2em;
  text-shadow: 2px 2px 1px #000;
}

ul audio {
  margin-top: .5em;
  position: absolute;
  bottom: 0;
  left: 0;
  height: 30px;
}

footer {
  display: flex;
  width: 100%;
  justify-content: center;
  color: #fff;
  background: #ddd;
  /* position: fixed; */
  bottom: 0;
  text-shadow: 1px 1px 1px #000;

}

footer a {
  text-decoration: none;
  color: #000;
  text-shadow: 1px 1px 1px #fff;
}

@media screen and (min-width: 800px) {
  body {
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .form-input {
    display: inline;
  }

  ul {
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap
  }

  ul li a {
    text-shadow: 0px 1px 1px #000;
  }
}

.song_img {
  width: 250px;
}
#songs li { 
  margin-bottom: 20px;
  width: 300px;
  border: 1px solid #ddd;
  background: #000;
  box-shadow: 0 5px 10px #FFF;
  position: relative;
}

#songs li:hover {
  transform: scale(1.2) translateY(-20px);

}